<template>
      <q-page class="flex flex-center column">
        <h3>Characters</h3>

        <div class="flex flex-center row q-gutter-md">
          <div v-for="character in characters" :key="character.id">
            <q-card class="my-card">
              <img :src="character.image">

              <q-card-section>
                <div class="text-h6">{{ character.name }}</div>
                <div class="text-subtitle2">{{ character.origin.name }}</div>
              </q-card-section>

              <div class="character-link">
                <router-link :to="'/character/' + character.id"></router-link>
              </div>

            </q-card>
          </div>
        </div>

        <div class="pagination">
          <q-pagination
            v-model="current"
            :max="maxPages"
            max-pages="5"
            direction-links
            flat
            color="grey"
            active-color="primary"
            v-on:click="changePage"
          />
        </div>

      </q-page>

  </template>
  
  <script>
  import { ref } from 'vue'
  import charactersService from '@/services/charactersService.ts';

  const initial_page = 1;

  async function getCharacters(page) {
    let allCharacters;
    await charactersService.getCharacterList(page).then(
        (response) => { 
            allCharacters = response.results;
        }
      )
    return allCharacters;
  }

  async function getMaxPages() {
    let allPages;
    await charactersService.getCharacterList().then(
        (response) => { 
            allPages = response.info.pages;
        }
      )
    return allPages;
  }
  
  export default {

    name: 'LayoutDefault',

    methods: {
      changePage: async function() {
        console.log(this.current);
        this.characters = await getCharacters(this.current);
      },
    },

    data() {
      return {
        characters: [],
        maxPages: 0,
      }
    },

    async created() {
      this.characters = await getCharacters(initial_page);
      this.maxPages = await getMaxPages();
      console.log(this.characters);
    },
  
    setup () {
      return {
        leftDrawerOpen: ref(false),
        current: ref(initial_page)
      }
    }
  }
  </script>
  