<template>
    <q-page class="flex flex-center column">

      <div class="flex flex-center">
        <div>
            <h3>{{ episode?.name }}</h3>
            <p>{{ episode?.air_date }}</p>
            <p>{{ episode?.episode }}</p>
        </div>

      </div>

    </q-page>

</template>

<script>
import { ref } from 'vue';
import { useRoute } from 'vue-router';
import episodesService from '@/services/episodesService.ts';

async function getEpisode(id) {
    let episode;
    await episodesService.getEpisode(id).then(
        (response) => {
            episode = response;
        }
    )
    return episode;
}

export default {

  name: 'LayoutDefault',

  data() {
    return {
      episode: null,
    }
  },

  async created() {
    const route = useRoute();
    this.episode = await getEpisode(route.params.id);
    console.log(this.episode);
  },

  setup () {
    return {
      leftDrawerOpen: ref(false),
    }
  }
}
</script>
