<template>
    <q-page class="flex flex-center column">

      <div class="flex flex-center">
        <div>
            <img :src="character?.image" :alt="character?.name">
            <h3>{{ character?.name }}</h3>
            <p>{{ character?.species }}</p>
            <p>{{ character?.gender }}</p>
            <p>{{ character?.origin.name }}</p>
            <p>{{ character?.location.name }}</p>
            <p>{{ character?.species }}</p>
            <ul>
                <li>Espisodes where you can see {{ character?.name }}:</li>
                <li v-for="(episode, index) in episodes" :key="index">
                  <router-link :to="'/episode/' + episode.id">{{ episode.name }}</router-link>
                </li>
            </ul>
        </div>

      </div>

    </q-page>

</template>

<script>
import { ref } from 'vue';
import { useRoute } from 'vue-router';
import charactersService from '@/services/charactersService.ts';
import episodesService from '@/services/episodesService.ts';

async function getCharacter(id) {
  let character;
  await charactersService.getSingleCharacter(id).then(
      (response) => { 
          character = response;
      }
    )
  return character;
}

async function getEpisode(id) {
    let episode;
    await episodesService.getEpisode(id).then(
        (response) => {
            episode = response;
        }
    )
    return episode;
}

export default {

  name: 'LayoutDefault',

  data() {
    return {
      character: null,
      episodes: [],
    }
  },

  async created() {
    const route = useRoute();
    this.character = await getCharacter(route.params.id);

    for (let i = 0; i < this.character.episode.length; i++) {
      this.episodes.push(await getEpisode(this.character.episode[i].match(/(?<=api\/episode\/).*$/)[0]));
    }
  },

  setup () {
    return {
      leftDrawerOpen: ref(false),
    }
  }
}
</script>
