<template>
    <q-page class="flex flex-center column">
      <h3>Episodes</h3>

      <div class="flex flex-center row q-gutter-md">
        <div v-for="episode in episodes" :key="episode.id">
          <q-card class="my-card">
            <q-card-section>
              <div class="text-h6">{{ episode.name }}</div>
              <div class="text-subtitle2">{{ episode.episode }}</div>
            </q-card-section>

            <div class="character-link">
              <router-link :to="'/episode/' + episode.id"></router-link>
            </div>

          </q-card>
        </div>
      </div>

      <div class="pagination">
        <q-pagination
          v-model="current"
          :max="maxPages"
          max-pages="5"
          direction-links
          flat
          color="grey"
          active-color="primary"
          v-on:click="changePage"
        />
      </div>

    </q-page>

</template>

<script>
import { ref } from 'vue'
import episodesService from '@/services/episodesService.ts';

const initial_page = 1;

async function getEpisodes(page) {
  let allCharacters;
  await episodesService.getEpisodes(page).then(
      (response) => { 
          allCharacters = response.results;
      }
    )
  return allCharacters;
}

async function getMaxPages() {
  let allPages;
  await episodesService.getEpisodes().then(
      (response) => { 
          allPages = response.info.pages;
      }
    )
  return allPages;
}

export default {

  name: 'LayoutDefault',

  methods: {
    changePage: async function() {
      this.episodes = await getEpisodes(this.current);
    },
  },

  data() {
    return {
      episodes: [],
      maxPages: 0,
    }
  },

  async created() {
    this.episodes = await getEpisodes(initial_page);
    this.maxPages = await getMaxPages();
  },

  setup () {
    return {
      leftDrawerOpen: ref(false),
      current: ref(initial_page)
    }
  }
}
</script>
